import { useState } from 'react'
import { Helmet } from 'react-helmet-async'

import {
  Box,
  Button,
  Link,
  TextField as MuiTextField,
  Paper,
  type TextFieldProps,
  Typography,
  styled,
  useMediaQuery,
  useTheme
} from '@mui/material'

import { useRequestPasswordResetEmail } from 'src/hooks/api/authHooks'

const PageWrapper = styled(Box)(
  () => `
      display: flex;
      justify-content: center;
      align-items: center;
  `
)

const TextField = (props: TextFieldProps) => <MuiTextField size="small" required fullWidth {...props} />

const FindPassword: React.FC = () => {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))

  const [values, setValues] = useState({ email: '' })
  const requestPasswordResetEmailMutation = useRequestPasswordResetEmail(values)

  const handleChange: React.ChangeEventHandler<HTMLInputElement> = event => {
    const newValues = { email: event.target.value }
    setValues(newValues)
  }
  const handleSubmit: React.FormEventHandler<HTMLFormElement> = async e => {
    e.preventDefault()

    requestPasswordResetEmailMutation.mutateAsync()
  }

  return (
    <PageWrapper>
      <Helmet>
        <title>비밀번호 찾기</title>
      </Helmet>
      <Paper
        square={matches ? false : true}
        sx={{
          my: theme.spacing(13),
          padding: 3,
          flex: 1,
          maxWidth: '360px',
          [theme.breakpoints.down('sm')]: {
            my: 0,
            py: 5,
            maxWidth: '100%'
          },
          height: { xs: '90vh', sm: 'inherit' }
        }}
      >
        <Typography component="h1" variant="h3" sx={{ mb: 2, textAlign: 'center' }}>
          비밀번호 찾기
        </Typography>
        <Box
          component="form"
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            '& .MuiTextField-root': { margin: 1 }
          }}
          onSubmit={handleSubmit}
        >
          <Typography variant="caption" sx={{ mb: 2, textAlign: 'center' }}>
            가입하신 이메일 주소를 입력해 주시면 비밀번호를 재설정할 수 있는 링크를 보내드립니다.
          </Typography>
          <TextField id="email" name="email" type="email" label="(학교) 이메일" onChange={handleChange} />

          <Button type="submit" variant="contained" fullWidth sx={{ mt: 1 }}>
            이메일 전송하기
          </Button>
          <Link href="/auth/login" sx={{ mt: 1 }}>
            돌아가기
          </Link>
        </Box>
      </Paper>
    </PageWrapper>
  )
}

export default FindPassword
